export default function normalizeUrl(url: string) {
  // Check and add 'https://' if it's not there
  if (!url.startsWith("http://") && !url.startsWith("https://")) {
    url = "https://" + url;
  }

  // Remove trailing slash if present
  if (url.endsWith("/")) {
    url = url.slice(0, -1);
  }

  return url;
}
