import { useEffect, useMemo, useState } from "react";
import axios from "axios";

import { CommentSection, StyleConfig } from "../CommentSection";
import { Article, Style } from "../interfaces";
import { CommentSectionContainerProps } from "./interface";
import { baseStyle } from "../base-style";
import mergeStyleData from "./helpers/mergeStyleData";
import normalizeUrl from "./normalizeUrl";
import { resetDiv, resetP } from "../reset-styles";

function CommentSectionContainer({
  appKey,
  articleId,
  styleId,
  currentUser,
  callbacks,
  apiBaseUrl: apiBaseUrlProp,
  apiPaths,
  sectionProps,
  formProps,
  statsBarProps,
  commentElementsProps,
  dir,
}: CommentSectionContainerProps) {
  const [article, setArticle] = useState<Article>();
  const [style, setStyle] = useState<Style>({
    _id: "DEFAULT",
    name: "DEFAULT",
    config: baseStyle,
  });
  const [styleConfig, setStyleConfig] = useState<StyleConfig>(baseStyle);
  const apiBaseUrl = useMemo(() => {
    if (!apiBaseUrlProp || typeof apiBaseUrlProp !== "string") return undefined;
    return normalizeUrl(apiBaseUrlProp);
  }, [apiBaseUrlProp]);

  // This useEffect should on run once to fetch the article details
  useEffect(() => {
    if (!apiBaseUrl) return;
    (async () => {
      try {
        const path =
          apiPaths?.fetchArticlePath ??
          `${apiBaseUrl}/replyke-articles/${articleId}`;
        const response = await axios.get(path, {
          params: {
            // app_key: appKey,
          },
        });

        if (response.status === 204) {
          throw new Error("Article document doesn't exist yet");
        }

        setArticle(response.data);
      } catch (err: unknown) {
        let message = "Failed to fetch article: ";
        if (err instanceof Error) {
          message += err.message;
        }
        console.log(message);
      }
    })();
  }, [articleId, apiBaseUrl, apiPaths]);

  // This useEffect should on run once to fetch the style details
  useEffect(() => {
    if (!styleId) return;

    (async () => {
      try {
        const response = await axios.get(
          `https://replyke.up.railway.app/styles/${styleId}`
        );

        if (response.status === 204) {
          throw new Error("Style document doesn't exist yet");
        }

        setStyle(response.data);
      } catch (err: unknown) {
        let message = "Failed to fetch style config: ";
        if (err instanceof Error) {
          message += err;
        }
        console.log(message);
      }
    })();
  }, [styleId]);

  useEffect(() => {
    mergeStyleData(
      style,
      setStyleConfig,
      sectionProps,
      formProps,
      statsBarProps,
      commentElementsProps
    );
  }, [style, sectionProps, formProps, statsBarProps, commentElementsProps]);

  if (!apiBaseUrl) {
    return (
      <div style={{ ...resetDiv, backgroundColor: "#E5E7EB", padding: "1rem" }}>
        <p style={{ ...resetP, color: "#EF4444" }}>
          Please provide a base URL to your API
        </p>
      </div>
    );
  }

  if (!callbacks.loginClickCallback) {
    return (
      <div style={{ ...resetDiv, backgroundColor: "#E5E7EB", padding: "1rem" }}>
        <p style={{ ...resetP, color: "#EF4444" }}>
          Please provide a login callback
        </p>
      </div>
    );
  }

  if (!articleId) {
    return (
      <div style={{ ...resetDiv, backgroundColor: "#E5E7EB", padding: "1rem" }}>
        <p style={{ ...resetP, color: "#EF4444" }}>
          Please provide an article ID
        </p>
      </div>
    );
  }

  return (
    <CommentSection
      appKey={appKey}
      articleId={articleId}
      article={article}
      style={styleConfig}
      setArticle={setArticle}
      apiBaseUrl={apiBaseUrl}
      apiPaths={apiPaths}
      callbacks={callbacks}
      currentUser={currentUser}
      dir={dir}
    />
  );
}

export default CommentSectionContainer;
