import { useState } from "react";
import axios from "axios";

import { StyledButton } from "../StyledButton";
import { CommentReplyBoxProps } from "./interface";
import { resetButton, resetDiv, resetP, resetTextInput } from "../reset-styles";

function CommentReplyBox({
  appKey,
  articleId,
  currentUser,
  parentComment,
  hideReply,
  addReply,
  apiBaseUrl,
  apiPaths,
  loginFirstMessage = "Please login first",
  replyTooShortMessage = "Reply is too short",
  backgroundColor,
  marginTop,
  borderRadius,
  borderColor,
  borderStyle,
  borderWidth,
  paddingLeft,
  paddingRight,
  paddingTop,
  paddingBottom,
  shadow,
  textareaPlaceholder = "Replying to",
  textareaTextColor,
  textareaTextSize,
  textareaDefaultRows = 2,
  cancelButtonText = "Cancel",
  cancelButtonFontSize,
  cancelButtonFontWeight,
  cancelButtonFontColor,
  replyButtonText = "Reply",
  replyButtonColor,
  replyButtonBorderRadius,
  replyButtonFontSize,
  replyButtonFontWeight,
  replyButtonFontColor,
  replyButtonPaddingX,
  replyButtonPaddingY,
  replyButtonShadow,
}: CommentReplyBoxProps) {
  const [body, setBody] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit() {
    if (isSubmitting) return;

    if (!currentUser) {
      setError(loginFirstMessage);
      return;
    }
    if (body.length < 1) {
      //TODO allow customizing length

      setError(replyTooShortMessage);
      return;
    }

    setError(null);

    setIsSubmitting(true);

    const newComment = {
      app_key: appKey,
      article_id: articleId,
      parent: parentComment._id,
      comment_body: body,
      author: currentUser,
    };

    try {
      const path =
        apiPaths?.postCommentPath ?? `${apiBaseUrl}/replyke-comments`;
      const response = await axios.post(path, newComment);

      addReply(response.data);
      setBody("");
      hideReply();
    } catch (err: unknown) {
      let message = "Failed to post a reply: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
    } finally {
      setIsSubmitting(false);
    }
  }

  return (
    <div
      style={{
        ...resetDiv,
        backgroundColor,
        marginTop,
        borderRadius,
        borderColor,
        borderStyle,
        borderWidth,
        paddingLeft,
        paddingRight,
        paddingTop,
        paddingBottom,
        boxShadow: shadow
          ? "0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)"
          : "none",
      }}
    >
      <textarea
        rows={textareaDefaultRows}
        placeholder={`${textareaPlaceholder} ${parentComment.author.name}`}
        value={body}
        onChange={(e) => setBody(e.target.value)}
        style={{
          ...resetTextInput,
          fontSize: textareaTextSize,
          color: textareaTextColor,
          paddingLeft: paddingLeft ? paddingLeft * 0.5 : 0,
          paddingRight: paddingRight ? paddingRight * 0.5 : 0,
          width: "100%",
          border: "none",
          outline: "none",
          backgroundColor: "transparent",
          scrollbarWidth: "thin",
          scrollbarColor: "#4B5563 #E5E7EB", // gray-600 for thumb and gray-200 for track
        }}
      />
      <div
        style={{
          ...resetDiv,
          display: "flex",
          justifyContent: "flex-end",
          gap: "0.75rem",
        }}
      >
        <button
          onClick={hideReply}
          style={{
            ...resetButton,
            fontSize: cancelButtonFontSize,
            fontWeight: cancelButtonFontWeight,
            color: cancelButtonFontColor,
          }}
        >
          {cancelButtonText}
        </button>
        <StyledButton
          text={replyButtonText}
          type="button"
          disabled={isSubmitting}
          buttonColor={replyButtonColor}
          borderRadius={replyButtonBorderRadius}
          fontSize={replyButtonFontSize}
          fontWeight={replyButtonFontWeight}
          fontColor={replyButtonFontColor}
          paddingX={replyButtonPaddingX}
          paddingY={replyButtonPaddingY}
          shadow={replyButtonShadow}
          onClick={handleSubmit}
        />
      </div>
      <div style={{ ...resetDiv, display: "flex", justifyContent: "flex-end" }}>
        {error && (
          <p
            style={{
              ...resetP,
              color: "red",
              marginTop: "0.25rem",
              marginRight: "0.25rem",
              fontSize: "0.875rem",
              textAlign: "right",
            }}
          >
            {error}
          </p>
        )}
      </div>
    </div>
  );
}

export default CommentReplyBox;
