import { useState, useRef, useEffect } from "react";
import { CommentMenuProps } from "./interface";
import { resetDiv, resetButton } from "../reset-styles";

function CommentMenu({
  showRemove,
  hideRemove,
  showEdit,
  hideEdit,
  buttonPadding,
  buttonBackgroundColor,
  buttonBorderRadius,
  buttonIconColor,
  containerWidth,
  containerBackgroundColor = "#fff",
  containerShadow,
  containerBorderRadius,
  containerDividerColor = "#fff",
  containerPaddingX,
  containerPaddingY,
  itemsFontSize,
  itemsTextColor,
  itemsPaddingX,
  itemsPaddingY,
  editText = "Edit",
  removeText = "Remove",
}: CommentMenuProps) {
  const [isMenuShowing, setIsMenuShowing] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node)
      ) {
        setIsMenuShowing(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <div
      ref={dropdownRef}
      style={{ ...resetDiv, position: "relative", height: "max-content" }}
    >
      <button
        onClick={() => setIsMenuShowing((ims) => !ims)}
        type="button"
        style={{
          ...resetButton,
          padding: buttonPadding,
          backgroundColor: buttonBackgroundColor,
          borderRadius: buttonBorderRadius,
          color: buttonIconColor,
          cursor: "pointer",
        }}
      >
        <svg
          className="w-5 h-5"
          aria-hidden="true"
          fill="currentColor"
          viewBox="0 0 20 20"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M6 10a2 2 0 11-4 0 2 2 0 014 0zM12 10a2 2 0 11-4 0 2 2 0 014 0zM16 12a2 2 0 100-4 2 2 0 000 4z"></path>
        </svg>
      </button>

      <div
        style={{
          ...resetDiv,
          zIndex: 10,
          position: "absolute",
          right: 0,
          top: "2.5rem",
          backgroundColor: containerBackgroundColor,
          borderRadius: containerBorderRadius,
          fontSize: itemsFontSize,
          color: itemsTextColor,
          padding: `${containerPaddingY || 0}px ${containerPaddingX || 0}px`,
          width: containerWidth,
          boxShadow: containerShadow
            ? "0 1px 2px 0 rgba(0, 0, 0, 0.1)"
            : "none",
          display: isMenuShowing ? "block" : "none",
        }}
      >
        <div
          onClick={() => {
            showEdit();
            hideRemove();
            setIsMenuShowing(false);
          }}
          className="hover:bg-gray-200"
          style={{
            ...resetDiv,
            padding: `${itemsPaddingY || 0}px ${itemsPaddingX || 0}px`,
            width: "100%",
            textAlign: "left",
            cursor: "pointer",
          }}
        >
          {editText}
        </div>
        <div
          style={{
            height: "1px",
            width: "100%",
            backgroundColor: containerDividerColor,
          }}
        />
        <div
          onClick={() => {
            showRemove();
            hideEdit();
            setIsMenuShowing(false);
          }}
          className="hover:bg-gray-200"
          style={{
            ...resetDiv,
            padding: `${itemsPaddingY || 0}px ${itemsPaddingX || 0}px`,
            width: "100%",
            textAlign: "left",
            cursor: "pointer",
          }}
        >
          {removeText}
        </div>
      </div>
    </div>
  );
}

export default CommentMenu;
