import axios from "axios";
import { BiLike, BiCommentDetail } from "react-icons/bi";
import { CommentActionsBarProps } from "./interface";
import { resetButton, resetDiv, resetP } from "../reset-styles";

function CommentActionsBar({
  comment,
  setComment,
  areRepliesVisible,
  toggleReplies,
  showReplyBox,
  currentUser,
  apiBaseUrl,
  apiPaths,
  likeActiveIconColor = "blue",
  likeIconColor = "black",
  commentIconColor = "black",
  repliesText = "Replies",
  hideRepliesText = "Hide",
  replyCtaText = "Reply",
  replyCtaColor,
  replyCtaFontSize,
  loginFirstMessage = "Please login first",
}: CommentActionsBarProps) {
  async function handleLike() {
    if (!currentUser) {
      alert(loginFirstMessage);
      return;
    }

    try {
      const path =
        apiPaths?.likeCommentPath ?? `${apiBaseUrl}/replyke-comments/like`;
      const response = await axios.post(path, {
        comment_id: comment._id,
        user_id: currentUser._id,
      });

      setComment(response.data);
    } catch (err) {
      let message = "Failed to like the comment: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
    }
  }

  async function handleUnlike() {
    if (!currentUser) {
      alert(loginFirstMessage);
      return;
    }

    try {
      const path =
        apiPaths?.unlikeCommentPath ?? `${apiBaseUrl}/replyke-comments/unlike`;
      const response = await axios.post(path, {
        comment_id: comment._id,
        user_id: currentUser._id,
      });

      setComment(response.data);
    } catch (err) {
      let message = "Failed to unlike the comment: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
    }
  }

  const userLikedComment =
    currentUser && comment.likes.includes(currentUser._id);

  return (
    <div
      style={{
        ...resetDiv,
        display: "flex",
        alignItems: "center",
        marginTop: "1rem",
      }}
    >
      {/* Like Icon and Text */}
      <div
        style={{
          ...resetDiv,
          display: "flex",
          cursor: "pointer",
          alignItems: "center",
          color: userLikedComment ? likeActiveIconColor : likeIconColor,
        }}
        onClick={userLikedComment ? handleUnlike : handleLike}
      >
        <BiLike
          style={{
            height: "20px",
            width: "20px",
            color: userLikedComment ? likeActiveIconColor : likeIconColor,
          }}
        />
        <div style={{ ...resetDiv, width: "0.25rem" }} />
        <p
          style={{
            ...resetP,
            fontSize: "0.875rem",
            color: userLikedComment ? likeActiveIconColor : likeIconColor,
            cursor: "pointer",
          }}
        >
          {comment.likes_count}
        </p>
      </div>

      {/* Space between comment and like */}
      <div style={{ ...resetDiv, width: "1.5rem" }} />

      {/* Comment Icon and Text */}
      {comment.replies_count > 0 && (
        <div
          style={{
            ...resetDiv,
            display: "flex",
            cursor: "pointer",
            alignItems: "center",
          }}
          onClick={toggleReplies}
        >
          <BiCommentDetail
            style={{
              width: "1.25rem",
              height: "1.25rem",
              color: commentIconColor,
            }}
          />
          <div style={{ ...resetDiv, width: "0.25rem" }} />
          <p
            style={{
              ...resetP,
              fontSize: "0.875rem",
              color: commentIconColor,
              cursor: "pointer",
            }}
          >
            {areRepliesVisible ? hideRepliesText : repliesText}
          </p>
        </div>
      )}

      <div style={{ ...resetDiv, flexGrow: 1 }} />

      {/* Reply CTA */}
      <button
        type="button"
        onClick={showReplyBox}
        style={{ ...resetButton, borderStyle: "none", display: "flex" }}
      >
        <BiCommentDetail
          style={{
            marginTop: "0.25rem",
            height: "1rem",
            width: "1rem",
            color: replyCtaColor,
            cursor: "pointer",
          }}
        />
        <div style={{ ...resetDiv, width: "0.25rem" }} />
        <p
          style={{
            ...resetP,
            fontSize: replyCtaFontSize,
            color: replyCtaColor,
            cursor: "pointer",
          }}
        >
          {replyCtaText}
        </p>
      </button>
    </div>
  );
}

export default CommentActionsBar;
