import { CommentElementsProps } from "../../Comment/interface";
import { SectionStyleProps, StyleConfig } from "../../CommentSection/interface";
import { FormStyleProps } from "../../Form";
import { StatsBarStyleProps } from "../../StatsBar";
import { Style } from "../../interfaces";

export default function mergeStyleData(
  style: Style,
  setStyleConfig: React.Dispatch<React.SetStateAction<StyleConfig>>,
  sectionProps?: SectionStyleProps,
  formProps?: FormStyleProps,
  statsBarProps?: StatsBarStyleProps,
  commentElementsProps?: CommentElementsProps,

) {
  const mergedStyle = {
    sectionProps: {
      ...style.config?.sectionProps,
      ...sectionProps,
    },
    formProps: {
      ...style.config?.formProps,
      ...formProps,
    },
    statsBarProps: {
      ...style.config?.statsBarProps,
      ...statsBarProps,
    },
    commentElementsProps: {
      containerProps: {
        ...style.config.commentElementsProps?.containerProps,
        ...commentElementsProps?.containerProps,
      },
      menuProps: {
        ...style.config.commentElementsProps?.menuProps,
        ...commentElementsProps?.menuProps,
      },
      editProps: {
        ...style.config.commentElementsProps?.editProps,
        ...commentElementsProps?.editProps,
      },
      removeProps: {
        ...style.config.commentElementsProps?.removeProps,
        ...commentElementsProps?.removeProps,
      },
      actionBarProps: {
        ...style.config.commentElementsProps?.actionBarProps,
        ...commentElementsProps?.actionBarProps,
      },
      replyBoxProps: {
        ...style.config.commentElementsProps?.replyBoxProps,
        ...commentElementsProps?.replyBoxProps,
      },
      headerProps: {
        ...style.config.commentElementsProps?.headerProps,
        ...commentElementsProps?.headerProps,
      },
    },
  };

  setStyleConfig(mergedStyle);
}
