import { useEffect, useState } from "react";
import axios from "axios";

import { Form } from "../Form";
import { StatsBar } from "../StatsBar";
import { Comment as CommentType } from "../interfaces";
import { CommentSectionProps } from "./interface";
import { Comment } from "../Comment";
import { resetDiv, resetP } from "../reset-styles";

function CommentSection({
  appKey,
  articleId,
  article,
  setArticle,
  style,
  currentUser,
  apiBaseUrl,
  apiPaths,
  callbacks: {
    loginClickCallback,
    commentAuthorClickCallback,
    currentUserClickCallback,
  },
  dir = "ltr",
}: CommentSectionProps) {
  const [comments, setComments] = useState<CommentType[]>([]);
  const [sortBy, setSortBy] = useState<"popular" | "newest" | "oldest">(
    "newest"
  );
  const [page, setPage] = useState(1);

  // This is the initial fetching of the comments
  useEffect(() => {
    (async () => {
      try {
        const path =
          apiPaths?.fetchCommentsPath ?? `${apiBaseUrl}/replyke-comments`;
        const response = await axios.get(path, {
          params: {
            app_key: appKey,
            article_id: articleId,
            sort_by: sortBy,
            page,
            limit: style.sectionProps.commentChunkSize || 5,
          },
        });
        const newComments = response.data;
        if (!Array.isArray(newComments)) {
          throw new Error("Invlid data returned for comments");
        }

        if (page === 1) {
          setComments(newComments);
        } else {
          setComments((prevComments) => [...prevComments, ...newComments]);
        }
      } catch (err: unknown) {
        let message = "Failed to fetch comments: ";
        if (err instanceof Error) {
          message += err.message;
        }
        console.log(message);
      }
    })();
  }, [appKey, articleId, sortBy, page, style, apiBaseUrl, apiPaths]);

  // Whenever the sortBy changes we need to refetch
  useEffect(() => {
    setPage(1);
    setComments([]);
  }, [sortBy]);

  return (
    <div
      dir={dir}
      style={{
        ...resetDiv,
        width: "100%",
        margin: "auto",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <Form
        appKey={appKey}
        articleId={articleId}
        apiBaseUrl={apiBaseUrl}
        apiPaths={apiPaths}
        currentUser={currentUser}
        addComment={(comment: CommentType) => {
          setComments((cms) => [comment, ...cms]);
          setArticle((ar) =>
            ar
              ? { ...ar, comments_count: ar.comments_count + 1 }
              : {
                  _id: articleId,
                  likes_count: 0,
                  likes: [],
                  comments_count: 1,
                  replies_count: 0,
                }
          );
        }}
        {...style.formProps}
      />
      <StatsBar
        appKey={appKey}
        article={article}
        articleId={articleId}
        currentUser={currentUser}
        apiBaseUrl={apiBaseUrl}
        apiPaths={apiPaths}
        sortBy={sortBy}
        setSortBy={setSortBy}
        setArticle={setArticle}
        loginClickCallback={loginClickCallback}
        avatarPlaceholderColors={style.sectionProps.avatarPlaceholderColors}
        currentUserClickCallback={currentUserClickCallback}
        {...style.statsBarProps}
      />
      <div style={{ ...resetDiv }}>
        {comments.map((c, i) => (
          <Comment
            appKey={appKey}
            onRemove={(comment) => {
              setComments((cms) => cms.filter((c) => c._id !== comment._id));
            }}
            setArticle={setArticle}
            articleId={articleId}
            index={i}
            currentUser={currentUser}
            apiBaseUrl={apiBaseUrl}
            apiPaths={apiPaths}
            comment={c}
            sortBy={sortBy}
            replyChunkSize={style.sectionProps.replyChunkSize}
            depth={0}
            commentAuthorClickCallback={commentAuthorClickCallback}
            elementProps={{
              ...style.commentElementsProps,
              headerProps: {
                ...style.commentElementsProps.headerProps,
                authorAvatarPlaceholderColors:
                  style.sectionProps.avatarPlaceholderColors,
              },
            }}
            key={c._id}
          />
        ))}
      </div>
      {article && article.comments_count > comments.length && (
        <p
          style={{
            ...resetP,
            width: "max-content",
            marginTop: "0.5rem",
            marginBottom: "0.5rem",
            fontSize: "0.875rem",
            fontWeight: "600",
            cursor: "pointer",
            color: style.sectionProps.loadMoreColor,
          }}
          onClick={() => setPage((p) => p + 1)}
        >
          {style.sectionProps.loadMoreText ?? "Load more.."}
        </p>
      )}
    </div>
  );
}

export default CommentSection;
