import { useEffect, useState } from "react";
import axios from "axios";

import { CommentMenu } from "../CommentMenu";
import { CommentReplyBox } from "../CommentReplyBox";
import { CommentEdit } from "../CommentEdit";
import { CommentProps } from "./interface";
import { Article, Comment as CommentType } from "../interfaces";
import { CommentRemove } from "../CommentRemove";
import { CommentHeader } from "../CommentHeader";
import { CommentActionsBar } from "../CommentActionsBar";
import { resetDiv, resetP } from "../reset-styles";

function Comment({
  appKey,
  articleId,
  onRemove,
  setArticle,
  currentUser,
  apiBaseUrl,
  apiPaths,
  index,
  comment: commentFromSection,
  sortBy,
  replyChunkSize = 3,
  depth,
  commentAuthorClickCallback = () =>
    console.log("Please pass down an author click callback."),
  elementProps: {
    containerProps,
    menuProps,
    editProps,
    removeProps,
    actionBarProps,
    replyBoxProps,
    headerProps,
  },
  elementProps,
}: CommentProps) {
  const commentID = commentFromSection._id;
  const [comment, setComment] = useState(commentFromSection);
  const [isRemoveShowing, setIsRemoveShowing] = useState(false);

  const [isReplyBoxShowing, setIsReplyBoxShowing] = useState(false);
  const [isEdit, setIsEdit] = useState(false);

  const [replies, setReplies] = useState<CommentType[]>([]);
  const [newReplies, setNewReplies] = useState<CommentType[]>([]);
  const [areRepliesVisible, setAreRepliesVisible] = useState(false);
  const [page, setPage] = useState(0);

  const isAuthor = currentUser?._id === comment.author._id;
  const isFirst = depth === 0;

  let topBorderProperties = {};

  if (isFirst && index !== 0) {
    topBorderProperties = {
      borderTopWidth: containerProps?.threadsSeperatorWidth,
      borderTopColor: containerProps?.threadsSeperatorColor,
      borderTopStyle: containerProps?.threadsSeperatorStyle,
      paddingTop: containerProps?.threadsSeperatorTopPadding,
    };
  }

  useEffect(() => {
    if (page === 0) return;

    (async () => {
      try {
        const path =
          apiPaths?.fetchCommentsPath ?? `${apiBaseUrl}/replyke-comments`;
        const response = await axios.get(path, {
          params: {
            // app_key: appKey,
            article_id: articleId,
            sort_by: sortBy,
            page,
            limit: replyChunkSize,
            parent: commentID,
          },
        });

        const newReplies: CommentType[] = response.data;

        if (!Array.isArray(newReplies)) {
          throw new Error("Invlid data returned for comments");
        }

        if (page === 1) {
          setReplies(newReplies);
        } else {
          setReplies((currentRpls) => {
            const filtered = newReplies.filter(
              (rpl: CommentType) =>
                !currentRpls.some((el) => el._id === rpl._id)
            );

            return [...currentRpls, ...filtered];
          });
        }
      } catch (err: unknown) {
        let message = "Failed to fetch replies: ";
        if (err instanceof Error) {
          message += err.message;
        }
        console.log(message);
      }
    })();
  }, [
    page,
    apiBaseUrl,
    apiPaths,
    articleId,
    commentID,
    replyChunkSize,
    sortBy,
  ]);

  useEffect(() => {
    if (!areRepliesVisible && newReplies.length > 0) {
      setReplies((rpls) => [...newReplies, ...rpls]);
      setNewReplies([]);
    }
  }, [areRepliesVisible, newReplies]);

  return (
    <div
      style={{
        ...resetDiv,
        marginLeft: isFirst ? 0 : containerProps?.replyIndent,
        marginBottom: containerProps?.marginBottom,
        paddingTop: containerProps?.paddingY,
        paddingBottom: containerProps?.paddingY,
        paddingLeft: containerProps?.paddingX,
        paddingRight: containerProps?.paddingX,
        ...topBorderProperties,
      }}
    >
      <div
        style={{
          ...resetDiv,
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          gap: "0.75rem", // Equivalent to Tailwind's 'gap-3'
          marginBottom: containerProps?.headerBodySpacing,
        }}
      >
        <CommentHeader
          comment={comment}
          commentAuthorClickCallback={commentAuthorClickCallback}
          {...headerProps}
        />
        {isAuthor && (
          <CommentMenu
            showRemove={() => setIsRemoveShowing(true)}
            hideRemove={() => setIsRemoveShowing(false)}
            showEdit={() => setIsEdit(true)}
            hideEdit={() => setIsEdit(false)}
            {...menuProps}
          />
        )}
      </div>
      {isEdit ? (
        <CommentEdit
          comment={comment}
          setComment={setComment}
          hideEdit={() => setIsEdit(false)}
          apiBaseUrl={apiBaseUrl}
          apiPaths={apiPaths}
          {...editProps}
        />
      ) : (
        <p
          style={{
            ...resetP,
            fontSize: containerProps?.commentFontSize,
            color: containerProps?.commentFontColor,
          }}
        >
          {comment.body}
        </p>
      )}

      {isAuthor && (
        <CommentRemove
          appKey={appKey}
          comment={comment}
          removeComment={(comment: CommentType, updatedArticle: Article) => {
            onRemove(comment);
            setArticle(updatedArticle);
          }}
          isRemoveShowing={isRemoveShowing}
          setIsRemoveShowing={setIsRemoveShowing}
          apiBaseUrl={apiBaseUrl}
          apiPaths={apiPaths}
          {...removeProps}
        />
      )}

      <CommentActionsBar
        comment={comment}
        setComment={setComment}
        areRepliesVisible={areRepliesVisible}
        toggleReplies={() => {
          setAreRepliesVisible((arv) => {
            if (!arv && page === 0) {
              setPage(1);
            }
            return !arv;
          });
        }}
        showReplyBox={() => setIsReplyBoxShowing(true)}
        currentUser={currentUser}
        apiBaseUrl={apiBaseUrl}
        apiPaths={apiPaths}
        {...actionBarProps}
      />

      {isReplyBoxShowing && (
        <CommentReplyBox
          appKey={appKey}
          articleId={articleId}
          currentUser={currentUser}
          parentComment={{
            _id: comment._id,
            author: { name: comment.author.name || "Anonymus" },
          }}
          hideReply={() => setIsReplyBoxShowing(false)}
          addReply={(reply: CommentType) => {
            setNewReplies((rpls) => [reply, ...rpls]);
            setAreRepliesVisible(true);
            setComment((c) => ({ ...c, replies_count: c.replies_count + 1 }));
            setArticle((ar) =>
              ar
                ? {
                    ...ar,
                    replies_count: ar.replies_count + 1,
                  }
                : undefined
            );
          }}
          apiBaseUrl={apiBaseUrl}
          apiPaths={apiPaths}
          {...replyBoxProps}
        />
      )}

      {/* All the comment replies */}
      {areRepliesVisible && (
        <div
          style={{
            ...resetDiv,
            paddingLeft: "0.75rem", // Equivalent to Tailwind's 'pl-3'
            marginTop: "1rem",
            borderLeftWidth: containerProps?.repliesSideBorderWidth,
            borderLeftStyle: containerProps?.repliesSideBorderStyle,
            borderLeftColor: containerProps?.repliesSideBorderColor,
          }}
        >
          {newReplies.map((c, i) => (
            <Comment
              onRemove={(comment) => {
                setNewReplies((nr) => nr.filter((r) => r._id !== comment._id));
                setComment((c) => ({
                  ...c,
                  replies_count: c.replies_count - 1,
                }));
              }}
              setArticle={setArticle}
              appKey={appKey}
              articleId={articleId}
              apiBaseUrl={apiBaseUrl}
              apiPaths={apiPaths}
              currentUser={currentUser}
              elementProps={elementProps}
              comment={c}
              sortBy={sortBy}
              depth={depth + 1}
              index={i}
              key={c._id}
            />
          ))}

          {replies.map((c, i) => (
            <Comment
              onRemove={(comment) => {
                setReplies((nr) => nr.filter((r) => r._id !== comment._id));
                setComment((c) => ({
                  ...c,
                  replies_count: c.replies_count - 1,
                }));
              }}
              setArticle={setArticle}
              appKey={appKey}
              articleId={articleId}
              apiBaseUrl={apiBaseUrl}
              apiPaths={apiPaths}
              currentUser={currentUser}
              elementProps={elementProps}
              comment={c}
              sortBy={sortBy}
              depth={depth + 1}
              index={i}
              key={c._id}
            />
          ))}

          {/* Load more prompt - condtional - only if there's more to load */}
          {replies.length + newReplies.length < comment.replies_count && (
            <p
              style={{
                ...resetP,
                marginTop: "0.5rem",
                marginBottom: "0.5rem",
                fontSize: "0.875rem",
                fontWeight: "600",
                cursor: "pointer",
                color: containerProps?.loadMoreColor,
              }}
              onClick={() => setPage((p) => p + 1)}
            >
              {containerProps?.loadMoreText ?? "Load more.."}
            </p>
          )}
        </div>
      )}
    </div>
  );
}

export default Comment;
