import axios from "axios";
import { BiLike, BiCommentDetail } from "react-icons/bi";
import { IoIosArrowDown } from "react-icons/io";
import BoringAvatar from "boring-avatars";
import { useState, useRef, useEffect } from "react";
import { StatsBarProps } from "./interface";
import {
  resetDiv,
  resetImg,
  resetP,
  resetButton,
  resetIl,
  resetUl,
} from "../reset-styles";

function StatsBar({
  appKey,
  article,
  articleId,
  currentUser,
  sortBy,
  setSortBy,
  setArticle,
  apiBaseUrl,
  apiPaths,
  loginFirstMessage = "Please login first",
  popularText = "Popular",
  newestFirstText = "Newest",
  oldestFirstText = "Oldest",
  sortByButtonTextColor,
  sortByButtonFontSize,
  sortByItemsTextColor,
  sortByItemsFontSize,
  sortByChevronSize,
  loginText = "Login",
  loginFontSize,
  loginFontColor,
  loginClickCallback = () => {
    console.log("Please pass down a login callback.");
  },
  currentUserClickCallback = () => {
    console.log("Please pass down a user click callback.");
  },
  paddingX,
  paddingY,
  iconsSize,
  marginBottom,
  likeIconColor = "black",
  likeActiveIconColor = "blue",
  likeCountFontSize,
  commentIconColor = "black",
  commentCountFontSize,
  borderBottomColor,
  borderBottomStyle,
  borderBottomWidth,
  avatarSize = 32,
  avatarBorderRadius,
  avatarPlaceholderColors,
  usernameFontSize,
  usernameFontWeight,
  usernameFontColor,
  itemsGap,
}: StatsBarProps) {
  const [sortByOpen, setSortByOpen] = useState(false);
  const dropdownRef = useRef<HTMLUListElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node)
      ) {
        setSortByOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  async function handleLike() {
    if (!currentUser) {
      alert(loginFirstMessage);
      return;
    }

    try {
      const path =
        apiPaths?.likeArticlePath ?? `${apiBaseUrl}/replyke-articles/like`;
      const response = await axios.post(path, {
        article_id: articleId,
        user_id: currentUser._id,
        app_key: appKey,
      });

      setArticle(response.data);
    } catch (err: unknown) {
      let message = "Failed to like the article: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
    }
  }

  async function handleUnlike() {
    if (!currentUser) {
      alert(loginFirstMessage);
      return;
    }

    try {
      const path =
        apiPaths?.unlikeArticlePath ?? `${apiBaseUrl}/replyke-articles/unlike`;
      const response = await axios.post(path, {
        article_id: articleId,
        user_id: currentUser._id,
        app_key: appKey,
      });

      setArticle(response.data);
    } catch (err: unknown) {
      let message = "Failed to unlike the article: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
    }
  }

  const userLikedArticle =
    currentUser && article && article.likes.includes(currentUser._id);

  let sortByText = "";

  if (sortBy === "popular") {
    sortByText = popularText;
  }

  if (sortBy === "newest") {
    sortByText = newestFirstText;
  }

  if (sortBy === "oldest") {
    sortByText = oldestFirstText;
  }

  const handleOpen = () => {
    setSortByOpen(!sortByOpen);
  };

  const SortByItem = ({
    text,
    onClick,
  }: {
    text: string;
    onClick: () => void;
  }) => {
    return (
      <li
        onClick={() => {
          onClick();
          setSortByOpen(false);
        }}
        className="bg-transparent hover:bg-gray-100"
        style={{
          ...resetIl,
          color: sortByItemsTextColor,
          fontSize: sortByItemsFontSize,
          padding: "0.25rem 1.25rem", // Tailwind px-3 py-1 equivalent
          width: "100%",
          cursor: "pointer",
          whiteSpace: "nowrap",
          transition: "background-color 0.15s ease-in-out",
        }}
      >
        {text}
      </li>
    );
  };

  return (
    <div
      style={{
        ...resetDiv,
        display: "flex",
        alignItems: "center",
        paddingTop: paddingY,
        paddingBottom: paddingY,
        paddingRight: paddingX,
        paddingLeft: paddingX,
        marginBottom,
        borderBottomWidth,
        borderBottomStyle,
        borderBottomColor,
      }}
    >
      {/* Like button and count */}
      <div
        onClick={userLikedArticle ? handleUnlike : handleLike}
        style={{
          ...resetDiv,
          display: "flex",
          alignItems: "center",
          cursor: "pointer",
          flexDirection: "row",
          direction: "ltr",
        }}
      >
        <BiLike
          style={{
            margin: 0,
            height: iconsSize,
            width: iconsSize,
            color: userLikedArticle ? likeActiveIconColor : likeIconColor,
          }}
        />
        <p
          style={{
            ...resetP,
            margin: "0 0.25rem",
            fontSize: likeCountFontSize,
            color: userLikedArticle ? likeActiveIconColor : likeIconColor,
            cursor: "pointer",
          }}
        >
          {article?.likes_count ?? 0}
        </p>
      </div>

      {/* Space between like and comment */}
      <div style={{ ...resetDiv, width: itemsGap }} />

      {/* Replis button and count */}
      <div
        style={{
          ...resetDiv,
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          cursor: "pointer",
          direction: "ltr",
        }}
      >
        <BiCommentDetail
          style={{
            margin: 0,
            height: iconsSize,
            width: iconsSize,
            color: commentIconColor,
          }}
        />
        <p
          style={{
            ...resetP,
            margin: "0 0.25rem",
            fontSize: commentCountFontSize,
            color: commentIconColor,
            cursor: "pointer",
          }}
        >
          {article ? article.comments_count + article.replies_count : 0}
        </p>
      </div>

      <div style={{ ...resetDiv, width: itemsGap }} />
      <div style={{ ...resetDiv, position: "relative" }}>
        <button
          onClick={handleOpen}
          style={{
            ...resetButton,
            display: "flex",
            alignItems: "center",
            margin: 0,
            color: sortByButtonTextColor,
          }}
        >
          <p
            style={{
              ...resetP,
              fontSize: sortByButtonFontSize,
              cursor: "pointer",
            }}
          >
            {sortByText.charAt(0).toUpperCase() + sortByText.slice(1)}
          </p>
          <IoIosArrowDown
            style={{
              margin: 0,
              marginLeft: 8,
              marginRight: 8,
              marginTop: 4,
              height: sortByChevronSize,
              width: sortByChevronSize,
            }}
          />
        </button>
        {sortByOpen ? (
          <ul
            ref={dropdownRef}
            style={{
              ...resetUl,
              position: "absolute",
              backgroundColor: "white",
              borderRadius: "0.25rem",
              border: "1px solid #D1D5DB",
              top: "2rem",
              left: "-0.25rem",
              cursor: "pointer",
              padding: "0.5rem 0",
            }}
          >
            <SortByItem
              text={newestFirstText}
              onClick={() => {
                setSortBy("newest");
              }}
            />
            <SortByItem
              text={oldestFirstText}
              onClick={() => {
                setSortBy("oldest");
              }}
            />
            <SortByItem
              text={popularText}
              onClick={() => {
                setSortBy("popular");
              }}
            />
          </ul>
        ) : null}
      </div>

      <div style={{ ...resetDiv, flexGrow: 1 }} />
      {currentUser ? (
        <div
          onClick={() => currentUserClickCallback(currentUser)}
          style={{
            ...resetDiv,
            display: "flex",
            alignItems: "center",
          }}
        >
          <p
            style={{
              ...resetP,
              padding: "0 0.5rem",
              fontWeight: usernameFontWeight,
              fontSize: usernameFontSize,
              color: usernameFontColor,
            }}
          >
            {currentUser.name ?? "Anonymus"}
          </p>
          {currentUser.img ? (
            <img
              src={currentUser.img}
              alt={currentUser.name}
              style={{
                ...resetImg,
                margin: "0 0.5rem",
                objectFit: "cover",
                borderRadius: avatarBorderRadius,
                height: avatarSize,
                width: avatarSize,
              }}
            />
          ) : (
            <div
              style={{
                ...resetDiv,
                overflow: "hidden",
                height: avatarSize,
                width: avatarSize,
                borderRadius: avatarBorderRadius,
              }}
            >
              <BoringAvatar
                size={avatarSize}
                name={currentUser._id}
                variant="beam"
                colors={
                  avatarPlaceholderColors && avatarPlaceholderColors.length > 1
                    ? avatarPlaceholderColors
                    : ["#EDA142", "#C20D90"]
                }
              />
            </div>
          )}
        </div>
      ) : (
        <p
          style={{
            ...resetP,
            fontSize: loginFontSize,
            color: loginFontColor,
            fontWeight: 600,
            cursor: "pointer",
          }}
          onClick={loginClickCallback}
        >
          {loginText}
        </p>
      )}
    </div>
  );
}

export default StatsBar;
