import { useState } from "react";
import axios from "axios";
import { StyledButton } from "../StyledButton";
import { CommentEditProps } from "./interface";
import { resetButton, resetDiv, resetP, resetTextInput } from "../reset-styles";

function CommentEdit({
  comment,
  setComment,
  hideEdit,
  apiBaseUrl,
  apiPaths,
  marginTop,
  backgroundColor = "white",
  borderRadius,
  borderColor,
  borderStyle,
  borderWidth,
  paddingLeft,
  paddingRight,
  paddingTop,
  paddingBottom,
  shadow,
  scrollbarThin,
  // scrollbarThumbColor = "#1f1f1f",
  // scrollbarTrackColor = "#d6d6d6",
  textareaPlaceholder = "Edit your comment..",
  textareaTextColor,
  textareaTextSize,
  textareaDefaultRows = 3,
  cancelButtonText = "Cancel",
  cancelButtonFontColor,
  cancelButtonFontSize,
  saveButtonText = "Save",
  saveButtonColor,
  saveButtonBorderRadius,
  saveButtonFontSize,
  saveButtonFontWeight,
  saveButtonFontColor,
  saveButtonPaddingX,
  saveButtonPaddingY,
  saveButtonShadow,
}: CommentEditProps) {
  const [commentBody, setCommentBody] = useState(comment.body);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function handleSave() {
    if (isSubmitting) return;

    if (commentBody.length < 1) {
      //TODO allow customizing length
      setError("Comment is too short"); //TODO allow customizing message
      return;
    }

    setError(null);
    setIsSubmitting(true);

    try {
      const path =
        apiPaths?.saveEditedCommentPath ?? `${apiBaseUrl}/replyke-comments`;
      await axios.patch(path, {
        comment_id: comment._id,
        update: commentBody,
      });

      setComment((c) => ({ ...c, body: commentBody }));
      hideEdit();
    } catch (err) {
      let message = "Failed to update a comment: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
      setError("Something went wrong");
    } finally {
      setIsSubmitting(false);
    }
  }

  return (
    <div
      style={{
        ...resetDiv,
        marginTop,
        borderRadius,
        backgroundColor,
        borderColor,
        borderStyle,
        borderWidth,
        paddingLeft,
        paddingRight,
        paddingTop,
        paddingBottom,
        boxShadow: shadow
          ? "0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)"
          : "none",
      }}
    >
      <textarea
        rows={textareaDefaultRows}
        placeholder={textareaPlaceholder}
        value={commentBody}
        onChange={(e) => setCommentBody(e.target.value)}
        style={{
          ...resetTextInput,
          backgroundColor,
          color: textareaTextColor,
          fontSize: textareaTextSize,
          paddingLeft: paddingLeft ? paddingLeft * 0.5 : 0,
          paddingRight: paddingRight ? paddingRight * 0.5 : 0,
          scrollbarWidth: scrollbarThin ? "thin" : "auto",
          scrollbarColor: `#1f1f1f #d6d6d6`,
          outline: "none",
          width: "100%",
          border: "none",
        }}
      />
      <div
        style={{
          ...resetDiv,
          display: "flex",
          justifyContent: "flex-end",
          gap: "0.75rem",
          marginTop: "0.375rem",
        }}
      >
        <button
          onClick={hideEdit}
          style={{
            ...resetButton,
            color: cancelButtonFontColor,
            fontSize: cancelButtonFontSize,
          }}
        >
          {cancelButtonText}
        </button>
        <StyledButton
          type="button"
          onClick={handleSave}
          disabled={isSubmitting}
          text={saveButtonText}
          buttonColor={saveButtonColor}
          borderRadius={saveButtonBorderRadius}
          fontSize={saveButtonFontSize}
          fontWeight={saveButtonFontWeight}
          fontColor={saveButtonFontColor}
          paddingX={saveButtonPaddingX}
          paddingY={saveButtonPaddingY}
          shadow={saveButtonShadow}
        />
      </div>
      {error && (
        <div
          style={{ ...resetDiv, display: "flex", justifyContent: "flex-end" }}
        >
          <p
            style={{
              ...resetP,
              color: "red",
              marginTop: "0.25rem",
              marginRight: "0.25rem",
              fontSize: "0.875rem",
              textAlign: "right",
            }}
          >
            {error}
          </p>
        </div>
      )}
    </div>
  );
}

export default CommentEdit;
