import { StyledButtonProps } from "./interface";
import { resetButton } from "../reset-styles";

function StyledButton({
  text,
  type,
  onClick,
  disabled,
  full,
  mt,
  buttonColor,
  borderRadius,
  fontSize,
  fontWeight,
  fontColor,
  paddingX,
  paddingY,
  shadow,
}: StyledButtonProps) {
  return (
    <button
      type={type}
      onClick={onClick}
      style={{
        ...resetButton,
        marginTop: mt,
        width: full ? "100%" : "auto",
        borderRadius: borderRadius,
        fontSize: fontSize,
        fontWeight: fontWeight,
        padding: `${paddingY || 0}px ${paddingX || 0}px`,
        color: disabled ? "#D1D5DB" : fontColor,
        backgroundColor: disabled ? "#6B7280" : buttonColor,
        cursor: disabled ? "default" : "pointer",
        boxShadow: shadow
          ? "0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)"
          : "none",
        transition: "box-shadow 0.15s ease-in-out",
        userSelect: "none",
      }}
    >
      {text}
    </button>
  );
}

export default StyledButton;
