import moment from "moment";
import BoringAvatar from "boring-avatars";
import { CommentHeaderProps } from "./interface";
import { resetDiv, resetP, resetImg } from "../reset-styles";

function CommentHeader({
  comment,
  commentAuthorClickCallback,
  authorNameColor,
  authorNameFontSize,
  authorAvatarSize = 40,
  authorAvatarBorderRadius,
  authorAvatarPlaceholderColors,
  dateFontSize,
  dateFontColor,
}: CommentHeaderProps) {
  return (
    <div
      style={{
        ...resetDiv,
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        width: "100%",
      }}
    >
      <div
        onClick={() =>
          commentAuthorClickCallback &&
          commentAuthorClickCallback(comment.author)
        }
        style={{
          ...resetDiv,
          display: "flex",
          alignItems: "center",
          cursor: commentAuthorClickCallback ? "pointer" : "default",
        }}
      >
        {comment.author.img ? (
          <img
            src={comment.author.img}
            alt={comment.author.name}
            style={{
              ...resetImg,
              width: authorAvatarSize,
              height: authorAvatarSize,
              borderRadius: authorAvatarBorderRadius,
            }}
          />
        ) : (
          <div
            style={{
              ...resetDiv,
              overflow: "hidden",
              width: authorAvatarSize,
              height: authorAvatarSize,
              borderRadius: authorAvatarBorderRadius,
            }}
          >
            <BoringAvatar
              size={authorAvatarSize}
              name={comment.author._id}
              variant="beam"
              colors={
                authorAvatarPlaceholderColors &&
                authorAvatarPlaceholderColors.length > 1
                  ? authorAvatarPlaceholderColors
                  : ["#EDA142", "#C20D90"]
              }
            />
          </div>
        )}
        <p
          style={{
            ...resetP,
            color: authorNameColor,
            fontSize: authorNameFontSize,
            paddingLeft: "0.5rem",
            paddingRight: "0.5rem",
          }}
        >
          {comment.author.name || "Anonymous"}
        </p>
      </div>
      <p
        style={{
          ...resetP,
          fontSize: dateFontSize,
          color: dateFontColor,
        }}
      >
        {moment(new Date(comment.created_at)).fromNow()}
      </p>
    </div>
  );
}

export default CommentHeader;
