import { useState } from "react";
import axios from "axios";
import { StyledButton } from "../StyledButton";
import { CommentRemoveProps } from "./interface";
import {resetButton, resetDiv, resetP} from '../reset-styles';

function CommentRemove({
  appKey,
  comment,
  removeComment,
  isRemoveShowing,
  setIsRemoveShowing,
  apiBaseUrl,
  apiPaths,
  promptText = "Are you sure you want to remove this comment?",
  promptTextColor = "",
  promptTextFontSize = 14,
  cancelButtonText = "Cancel",
  cancelButtonFontSize,
  cancelButtonFontWeight,
  cancelButtonFontColor,
  confirmButtonText = "Remove",
  confirmButtonColor = "#EF4444",
  confirmButtonBorderRadius,
  confirmButtonFontSize,
  confirmButtonFontWeight,
  confirmButtonFontColor,
  confirmButtonPaddingX,
  confirmButtonPaddingY,
  confirmButtonShadow,
}: CommentRemoveProps) {
  const [isSubmitting, setIsSubmitting] = useState(false);

  async function handleRemove() {
    setIsSubmitting(true);
    try {
      const path =
        apiPaths?.removeCommentPath ?? `${apiBaseUrl}/replyke-comments`;
      const updatedArticle = await axios.delete(path, {
        data: { app_key: appKey, comment_id: comment._id },
      });

      removeComment(comment, updatedArticle.data);
    } catch (err: unknown) {
      let message = "Failed to delete comment: ";
      if (err instanceof Error) message += err.message;
      console.log(message);
      setIsSubmitting(false);
    }
  }

  return (
    <div
      style={{
        ...resetDiv,
        position: 'relative',
        overflow: 'hidden',
        width: '100%',
        transition: 'all',
        height: isRemoveShowing ? '80px' : 0
      }}
    >
      <div
        style={{
          ...resetDiv,
          paddingTop: '0.75rem', // Tailwind pt-3 equivalent
          marginTop: '0.75rem', // Tailwind mt-3 equivalent
          borderTop: '1px solid', // Tailwind border-t equivalent
          display: 'flex',
          alignItems: 'center',
          gap: '1rem', // Tailwind gap-4 equivalent
        }}
      >
        <p
          style={{
            ...resetP,
            color: promptTextColor,
            fontSize: `${promptTextFontSize}px`,
            flexGrow: 1,
          }}
        >
          {promptText}
        </p>
        <button
          onClick={() => setIsRemoveShowing(false)}
          style={{
            ...resetButton,
            color: cancelButtonFontColor,
            fontSize: `${cancelButtonFontSize}px`,
            fontWeight: cancelButtonFontWeight,
          }}
        >
          {cancelButtonText}
        </button>
        <StyledButton
          onClick={handleRemove}
          text={confirmButtonText}
          type="button"
          disabled={isSubmitting}
          buttonColor={confirmButtonColor}
          borderRadius={confirmButtonBorderRadius}
          fontSize={confirmButtonFontSize}
          fontWeight={confirmButtonFontWeight}
          fontColor={confirmButtonFontColor}
          paddingX={confirmButtonPaddingX}
          paddingY={confirmButtonPaddingY}
          shadow={confirmButtonShadow}
        />
      </div>
    </div>
  );
}

export default CommentRemove;
