import { useState } from "react";
import axios from "axios";

import { StyledButton } from "../StyledButton";
import { FormProps } from "./interface";
import { resetDiv, resetP, resetTextInput } from "../reset-styles";

function Form({
  appKey,
  articleId,
  addComment,
  currentUser,
  apiBaseUrl,
  apiPaths,
  loginFirstMessage = "Please login first",
  commentTooShortMessage = "Comment is too short",
  backgroundColor,
  borderRadius,
  borderColor,
  borderStyle,
  borderWidth,
  paddingLeft,
  paddingRight,
  paddingTop,
  paddingBottom,
  shadow,
  textareaPlaceholder = "Write a comment...",
  textareaTextColor,
  textareaTextSize,
  textareaDefaultRows,
  buttonMarginTop,
  buttonBorderRadius,
  buttonFontSize,
  buttonFontWeight,
  buttonFontColor,
  buttonPaddingX,
  buttonPaddingY,
  buttonColor,
  buttonText = "Post comment",
  buttonFullWidth,
  buttonShadow,
}: FormProps) {
  const [body, setBody] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function handleSubmit() {
    if (isSubmitting) return;

    if (!currentUser) {
      setError(loginFirstMessage);
      return;
    }
    if (body.length < 1) {
      //TODO allow customizing length
      setError(commentTooShortMessage);
      return;
    }

    setError(null);

    setIsSubmitting(true);

    const newComment = {
      app_key: appKey,
      article_id: articleId,
      comment_body: body,
      author: currentUser,
    };

    try {
      const path =
        apiPaths?.postCommentPath ?? `${apiBaseUrl}/replyke-comments`;
      const response = await axios.post(path, newComment);
      addComment(response.data);
      setBody("");
    } catch (err: unknown) {
      let message = "Failed to submit a new comment: ";
      if (err instanceof Error) {
        message += err.message;
      }
      console.log(message);
    } finally {
      setIsSubmitting(false);
    }
  }

  return (
    <div
      style={{
        ...resetDiv,
        backgroundColor,
        borderColor,
        borderStyle,
        borderWidth,
        paddingLeft,
        paddingRight,
        paddingTop,
        paddingBottom,
        borderRadius,
        boxShadow: shadow ? "0 0 4px 0 rgba(0, 0, 0, 0.15)" : "none", // Tailwind 'shadow' equivalent
      }}
    >
      <textarea
        rows={textareaDefaultRows}
        value={body}
        onChange={(e) => setBody(e.target.value)}
        placeholder={textareaPlaceholder}
        required
        style={{
          ...resetTextInput,
          backgroundColor,
          color: textareaTextColor,
          fontSize: textareaTextSize,
          paddingLeft: paddingLeft ? paddingLeft * 0.5 : 0,
          paddingRight: paddingRight ? paddingRight * 0.5 : 0,
          width: "100%",
          border: "none",
          outline: "none",
          scrollbarWidth: "thin",
          scrollbarColor: "#4B5563 #E5E7EB", // scrollbar-thumb-gray-600 and scrollbar-track-gray-200 equivalent
        }}
      />
      <div style={{ ...resetDiv, display: "flex", justifyContent: "flex-end" }}>
        <StyledButton
          type="button"
          text={buttonText}
          mt={buttonMarginTop}
          buttonColor={buttonColor}
          borderRadius={buttonBorderRadius}
          fontSize={buttonFontSize}
          fontWeight={buttonFontWeight}
          fontColor={buttonFontColor}
          paddingX={buttonPaddingX}
          paddingY={buttonPaddingY}
          full={buttonFullWidth}
          shadow={buttonShadow}
          onClick={handleSubmit}
          disabled={isSubmitting}
        />
      </div>
      {error && (
        <div
          style={{ ...resetDiv, display: "flex", justifyContent: "flex-end" }}
        >
          <p
            style={{
              ...resetP,
              color: "red",
              marginTop: "0.25rem",
              marginRight: "0.25rem",
              fontSize: "0.875rem",
              textAlign: "right",
            }}
          >
            {error}
          </p>
        </div>
      )}
    </div>
  );
}

export default Form;
